
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* //navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  /* border-bottom: solid gray !important; */
}

.nav-link {
  font-weight: 500 !important;
  color: #f1750b ;
}

nav .active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 900;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
  color: #f1750b ;
}

/* =============================
==========footer============= */

.footer {
  color: white;
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000);
 margin-top: 200px;
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* uidhfd hdhfdsh   */

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.userDashboard{
  margin-top: 100px;
}

.profileArea{
  background-color: #f1750b;
  padding: 10px;
  border-radius: 5px 5px 0 0;
}
.profileArea img{
  height: 100px;
  width: 100px;
  border-radius: 50%;
  margin-top: 20px;
}
.profileArea h4{
  color: #fff;
  margin-top: 10px;
  font-weight: 700;
}
.profileArea h6{
  color: #fff;
  margin-top: 10px;
  font-weight: 700;
}

.ProfileNav{
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

.ProfileNav nav a{
  color: #000;
  font-size:15px
}

.ProfileNav .list-group-item:first-child {
  border-top-left-radius: 0; 
  border-top-right-radius: 0;
}

.ProfileNav .list-group-item.active {
  z-index: 2;
  color: var(--bs-list-group-active-color);
  background-color:#000 ;
  border-color: #000;
}


